  margin:15px 15px 30px
}

.giftcard__wrap img{
    position:relative;
    display:block;
    border-radius:10px;
    z-index:2;
  }

.giftcard__code{
  position:absolute;
  bottom:30px;
  text-align:center;
  width:100%;
  z-index:50;
}

.giftcard__code--medium{
  font-size:.875em;
}

.giftcard__code--small{
  font-size:.75em;
}

.giftcard__code__inner{
  display:inline-block;
  vertical-align:baseline;
  background-color:#fff;
  padding:.5em;
  border-radius:4px;
  max-width:450px;
  box-shadow:0 0 0 1px rgba(0, 0, 0, 0.1)
}

.giftcard__code--small .giftcard__code__inner{
    overflow:auto
}

.giftcard__code__text{
  font-weight:400;
  font-size:1.875em;
  text-transform:uppercase;
  border-radius:2px;
  border:1px dashed;
  border-color:#e8e8e1;
  border-color:var(--colorBorder);
  padding:.4em .5em;
  display:inline-block;
  vertical-align:baseline;
  color:#000000;
  color:var(--colorTextBody);
  line-height:1
}

.disabled .giftcard__code__text{
    color:#999;
    text-decoration:line-through
}

.giftcard__amount{
  position:absolute;
  top:0;
  right:0;
  color:#fff;
  font-size:2.75em;
  line-height:1.2;
  padding:15px;
  z-index:50
}

.giftcard__amount strong{
    display:block;
    text-shadow:3px 3px 0 rgba(0, 0, 0, 0.1);
  }

.giftcard__amount--medium{
  font-size:2em;
}

.tooltip{
  display:block;
  position:absolute;
  top:-50%;
  right:50%;
  margin-top:16px;
  z-index:3;
  color:#fff;
  text-align:center;
  white-space:nowrap
}

.tooltip:before{
    content:"";
    display:block;
    position:absolute;
    left:100%;
    bottom:0;
    width:0;
    height:0;
    margin-left:-5px;
    margin-bottom:-5px;
    border-left:8px solid transparent;
    border-right:8px solid transparent;
    border-top:5px solid #333;
    border-top:5px solid rgba(51, 51, 51, 0.9);
  }

.tooltip__label{
  display:block;
  position:relative;
  right:-50%;
  border:none;
  border-radius:4px;
  background:#333;
  background:rgba(51, 51, 51, 0.9);
  min-height:14px;
  font-weight:400;
  font-size:12px;
  text-decoration:none;
  line-height:16px;
  text-shadow:none;
  padding:.5em .75em;
  margin-left:.25em
}

.tooltip__label small{
    text-transform:uppercase;
    letter-spacing:.1em;
    color:#b3b3b3;
    font-size:.875em;
  }

.giftcard__instructions{
  text-align:center;
  margin:0 15px 30px;
}

.giftcard__actions{
  position:relative;
  text-align:center;
  overflow:hidden;
  padding-bottom:1em;
}

.template-giftcard .action-link{
  position:absolute;
  left:15px;
  top:50%;
  font-size:0.875em;
  font-weight:700;
  display:block;
  padding-top:4px;
  text-transform:uppercase;
  letter-spacing:.2em;
  margin-top:-10px
}

.template-giftcard .action-link:focus,.template-giftcard .action-link:hover{
    color:#000000;
    color:var(--colorTextBody);
  }

.template-giftcard .action-link__print{
  display:inline-block;
  vertical-align:baseline;
  width:17px;
  height:17px;
  vertical-align:middle;
  margin-right:10px;
  opacity:1;
  background-image:url(//cdn.shopify.com/s/files/1/0603/2677/2912/t/11/assets//cdn.shopify.com/s/assets/gift-card/icon-print-164daa1ae32d10d1f9b83ac21b6f2c70.png?3105);
  background-repeat:no-repeat;
  background-position:0 0;
}

.giftcard__footer{
  text-align:center;
  padding:60px 0;
}

#QrCode img{
    padding:30px;
    border:1px solid;
    border-color:#e8e8e1;
    border-color:var(--colorBorder);
    border-radius:4px;
    margin:0 auto 30px;
  }

