  .giftcard{
    font-size:12px;
  }

  .giftcard-header{
    padding:30px 0;
  }

  .header-logo{
    font-size:2em;
  }

  .giftcard__border{
    padding:15px;
  }

  .giftcard__actions{
    padding:15px;
  }

