/* Variant Css  */

.variant_value {
  display: none;
}

.variant_select {
  position: fixed;
  padding: 0;
  margin: 0;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 255;
  flex-direction: row;
  overflow-y: scroll;
  /* gap:0px; */
  /* background : rgba(0,0,0,0.5); */
}

.variant_select_a{
  display: none;
}

.variant_select_b{
  display: none;
}


.variant_dark_modal {
  background: #343334;
  opacity: 0.5;
  width: 100vw;
  position: fixed;
  height: 100vh;
  left: 0px;
  top: 0px;
}

.variant_list_pick {
  display: flex;
  align-items: center;
  padding: 16px 24px;
  gap: 10px;
  position: absolute;
  width: 496px;
  min-height: 1277px;
  right: -496px;
  top: 0px;
  background: white;
  flex-wrap: wrap;
  align-content: flex-start;
  transition: 300ms ease-out;
  animation-delay: 300ms;
  z-index: 300;
}

.variant_select.active {
  display: flex;
}
.variant_select.active .variant_list_pick {
  overflow: scroll;
  animation-delay: 300ms;
  /*transform: translateX(-496px);*/
  transition: 300ms ease-out;
}


.variant_modify{
  text-decoration: underline;
  cursor: pointer;
}

/* Composition Css */

#composition-section-product{
  position: relative;
  padding-top: 0;
}
#composition-section-product > .index-section:first-child {
  padding-top: 0;
}
#composition-section-product .slick-arrow {
  top: 50%;
}
#composition-section-product .slick-prev {
  transform: translateY(-50%);
  left: 0;
}
#composition-section-product .slick-next {
  right: 0;
}


@media only screen and (min-width: 769px){
  #composition-section-product .index-section {
    padding-top: 0;
    margin-block: 0 32px;
  }

  #composition-section-product .index-section>.page-width:first-child {
    position: relative;
    z-index: 1;
    background: #fff;
  }

  #composition-section-product .section-header {
    margin-bottom: 0;
    padding-bottom: 24px;
  }

  #composition-section-product .section-header .section-header__title {
    font-size: 48px;
  }
  #composition-section-product .grid-item-price {
    margin-top: 0;
  }

}

.grid__image-ratio_compositions img {
  top: 0;
  left: 0;
  width: 91%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  mix-blend-mode: darken;
}

.grid__image-ratio_compositions {
  position: relative;
  background-color: none;
}


.product-compositions a .grid-item__meta {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  flex: 1;
  height: 100%;
}
#composition-section-product .product-compositions a .grid-item__meta {
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-inline: var(--pageWidthPadding);
}

#variant_box_pp {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 0px;
  gap: 16px;
  width: 100%;
  margin-bottom: 24px;
}

.current_variant_state {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  position: relative;
  padding: 8px 16px;
  max-width: 49%;
  height: 77px;
  background: #FFF;
  border: 1px solid #c4c4c4;
  border-radius: 4px;
  min-width: 190px;
  flex: 1;
  cursor: pointer;
}
.current_variant_state::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 24px;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  background: url(//cdn.shopify.com/s/files/1/0603/2677/2912/t/11/assets/chevron-right-tn.svg) center no-repeat;
}

.current_variant_information {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  gap: 4px;
  width: 221px;
  height: 61px;
}

.variant_type {
  width: 52px;
  height: 17px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #000;
}

.pp_variant_value {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0px;
  gap: 8px;
  width: 100%;
  height: 19px;
}

.first_variant_value {
  width: 35px;
  height: 19px;
  color: #000;
  font-style: normal;
  font-weight: 700;
