  font-family: 'Helvetica';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #343334;
}

.variant_unit{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 16px;
  gap: 8px;
  border: 1px solid #D7D7DA;
  background: #FFFFFF;
}

.variant_unit:hover{
  background: #B6C1B3; 
