@media only screen and (max-width:768px){
  .variant_lp_box > button {
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 1px;
  }
  .variant_lp_box > button:hover,
  .variant_lp_box > button:focus {
    outline: 1px solid #000 !important;
  }

  .variant_lp_box > button:not(:nth-of-type(1)):not(:nth-of-type(2)):not(:last-child){
    display: none;
    width: 0%;
    opacity: 0;
  }
  
  #variant_box_pp{
    flex-direction: column;
  }

  .current_variant_state{
    width: 100%;
  }
  .current_variant_state {
    max-width: 100%;
  }

  .variant_list_pick {
    width: 90%;
  }
}
